<template>
    <div class="window">
        <el-row :gutter="20" class="functionalArea">
            <el-col :span="6">
                <el-button-group>
                    <el-button type="primary" icon="el-icon-plus" @click="addBtnFn"></el-button>
                    <el-button type="primary" icon="el-icon-share"></el-button>
                    <el-button type="primary" icon="el-icon-delete" @click="clearBtnFn"></el-button>
                </el-button-group>
            </el-col>
            <el-col :span="6" :offset="12" v-show="false">
                <el-input placeholder="请输入内容" v-model="searchInput" clearable>
                    <template slot="append">
                        <el-button type="success" plain>搜索</el-button>
                    </template>
                </el-input>
            </el-col>
        </el-row>

        <el-table
                ref="multipleTable"
                :data="tableData"
                style="width: 100%"
                :header-cell-style="headerCellStyle"
                :cell-style="cellStyle"
                @select="handleSelectionChange"
                @select-all="selectAllFn"
        >
            <el-table-column
                    type="selection"
                    width="55">
            </el-table-column>
            <el-table-column
                    label="序号"
                    width="60">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.index }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="窗口位置"
                    width="200">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.location }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="主题"
                    width="200">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.subject }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="地点"
                    width="200">
                <template slot-scope="scope">
                    <span style="margin-left: 10px">{{ scope.row.addr }}</span>
                </template>
            </el-table-column>
            <el-table-column
                    label="操作"
                    min-width="240">
                <template slot-scope="scope">
                    <el-button
                            size="mini"
                            @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
                    <el-button
                            size="mini"
                            type="danger"
                            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加窗口" :visible.sync="addFormVisible" width="800px">
            <el-form :model="form">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="窗口位置" :label-width="formLabelWidth">
                            <el-input v-model="form.location" placeholder="请输入窗口" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="地点" :label-width="formLabelWidth">
                            <el-input v-model="form.addr" placeholder="请输入地点" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="主题" :label-width="formLabelWidth">
                            <el-select v-model="form.subject_id" placeholder="请选择部门" style="width:100%;">
                                <el-option v-for="(item, index) in themeList" :key="index" :label="item.label" :value="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addSureFn">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog title="编辑窗口" :visible.sync="fixFormVisible" width="800px">
            <el-form :model="form1">
                <el-row :gutter="20">
                    <el-col :span="12">
                        <el-form-item label="窗口位置" :label-width="formLabelWidth">
                            <el-input v-model="form1.location" placeholder="请输入窗口" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="地点" :label-width="formLabelWidth">
                            <el-input v-model="form1.addr" placeholder="请输入地点" autocomplete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="主题" :label-width="formLabelWidth">
                            <el-select v-model="form1.subject_id" placeholder="请选择部门" style="width:100%;">
                                <el-option v-for="(item, index) in themeList" :key="index" :label="item.label" :value="item.val"></el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="fixFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="fixSureFn">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

export default {
  name: 'ATheme',
  data () {
    return {
      searchInput: '',
      tableData: [],
      jurisdictionData: [],

      themeList: [],

      addFormVisible: false,
      formLabelWidth: '120px',
      form: {
        location: '',
        subject_id: '',
        addr: ''
      },
      fixFormVisible: false,
      form1: {
        location: '',
        subject_id: '',
        addr: '',
        id: ''
      },

      selectIds: [],

      notifyPromise: Promise.resolve()
    }
  },
  watch: {
  },
  created: function () {
    this.getThemeFn()
    this.getWindowFn()
  },
  methods: {
    addBtnFn () {
      this.addFormVisible = true
    },
    addSureFn () {
      let temp = {
        addr: this.form.addr.replace(/(^\s*)|(\s*$)/g, ''),
        location: this.form.location.replace(/(^\s*)|(\s*$)/g, ''),
        subject_id: this.form.subject_id,
        user_id: sessionStorage.user_id
      }
      if (temp.addr !== '' && temp.location !== '' && temp.subject_id !== '') {
        this.addFn(temp)
      } else {
        if (temp.addr === '') {
          this.notifyFn('提示', '地点输入有误')
        }
        if (temp.location === '') {
          this.notifyFn('提示', '窗口位置输入有误')
        }
        if (temp.subject_id === '') {
          this.notifyFn('提示', '请选择主题')
        }
      }
    },
    // 通知，解决element-ui，同时调用notify时，通知重叠的问题
    notifyFn (title, msg, type = 'warning') {
      this.notifyPromise = this.notifyPromise.then(this.$nextTick).then(() => {
        this.$notify({
          title: title,
          message: msg,
          type: type,
          dangerouslyUseHTMLString: true,
          duration: 5000,
          position: 'top-right'
        })
      })
    },
    handleEdit (index, row) {
      console.log(index, row)
      this.fixFormVisible = true
      this.form1.location = row.location
      this.form1.addr = row.addr
      let id = ''
      this.themeList.forEach(function (val, i) {
        if (val.label === row.subject) {
          id = val.val
        }
      })
      this.form1.subject_id = id
      this.form1.id = row.id
    },
    fixSureFn () {
      let temp = {
        id: this.form1.id,
        addr: this.form1.addr.replace(/(^\s*)|(\s*$)/g, ''),
        location: this.form1.location.replace(/(^\s*)|(\s*$)/g, ''),
        subject_id: this.form1.subject_id,
        user_id: sessionStorage.user_id
      }
      if (temp.name !== '' && temp.seq !== '') {
        this.fixFn(temp)
      } else {
        if (temp.addr === '') {
          this.notifyFn('提示', '地点输入有误')
        }
        if (temp.location === '') {
          this.notifyFn('提示', '窗口位置输入有误')
        }
        if (temp.subject_id === '') {
          this.notifyFn('提示', '请选择主题')
        }
      }
    },
    clearBtnFn () {
      if (this.selectIds.length) {
        this.$confirm('确定删除所选窗口', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let params = {
            id_list: this.selectIds,
            user_id: sessionStorage.user_id
          }
          this.deleteSingleFn(params)
        }).catch(() => {
          this.notifyFn('提示', '已取消删除')
        })
      } else {
        this.notifyFn('提示', '未选择窗口')
      }
    },
    handleDelete (index, row) {
      console.log(row)
      this.$confirm('确定删除窗口：' + row.location, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = {
          id_list: [row.id],
          user_id: sessionStorage.user_id
        }
        this.deleteSingleFn(params)
      }).catch(() => {
        this.notifyFn('提示', '已取消删除')
      })
    },
    async getThemeFn () {
      try {
        let params = {}
        let res = await this.$api.unsplash.getSubjects(params)
        console.log(res)
        this.themeList = []
        if (res.errno === '0') {
          for (let i = 0; i < res.data.subjects.length; i++) {
            this.themeList.push({
              label: res.data.subjects[i].name,
              val: res.data.subjects[i].id
            })
          }
          // this.themeList.push({
          //     label: '所有',
          //     val: -1
          // })
          //
          // this.selectThemeId=this.themeList[0].val
          // this.selectThemeName=this.themeList[0].label
          // this.getUserFn(this.selectThemeName);
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async getWindowFn () {
      let params = {}
      try {
        let res = await this.$api.unsplash.getWindow(params)
        console.log(res)
        if (res.errno === '0') {
          this.tableData = this.addSequence(res.data)
        } else {
          this.notifyFn('提示', '获取窗口失败')
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async addFn (params) {
      try {
        let res = await this.$api.unsplash.postAddWindow(params)
        if (res.errno === '0') {
          this.addFormVisible = false
          this.notifyFn('提示', '增加窗口成功')
          this.getWindowFn()
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async fixFn (params) {
      try {
        let res = await this.$api.unsplash.postfixWindow(params)
        if (res.errno === '0') {
          this.fixFormVisible = false
          this.notifyFn('提示', '修改窗口成功')
          this.getWindowFn()
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    async deleteSingleFn (params) {
      try {
        let res = await this.$api.unsplash.postClearWindow(params)
        if (res.errno === '0') {
          this.notifyFn('提示', '删除成功')
          this.selectIds = []
          this.getWindowFn()
        } else {
          this.notifyFn('提示', res.errmsg)
        }
      } catch (e) {
        this.notifyFn('提示', '服务器错误')
        console.log(e)
      }
    },
    handleSelectionChange (selection, row) { // 单击
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    selectAllFn (selection) { // 全选
      console.log(selection)
      this.selectIds = []
      for (let i = 0; i < selection.length; i++) {
        this.selectIds.push(selection[i].id)
      }
    },
    headerCellStyle ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex > -1) {
        return {
          'text-align': 'center'
        }
      }
    },
    cellStyle ({ row, column, rowIndex, columnIndex }) {
      if (columnIndex >= 0) {
        return {
          'text-align': 'center'
        }
      }
    },
    myAlert (txt, title, self) {
      self.$alert(txt, title, {
        confirmButtonText: '确定',
        callback: action => {
        }
      })
    },
    addSequence (temp) {
      for (let i = 0; i < temp.length; i++) {
        temp[i].index = i + 1
      }
      return temp
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    .window{
        .functionalArea{
            height: 80px;
        }
        .headerCellStyle{
            text-align: center;
        }
        .el-dialog__body {
            padding: 30px 60px 30px 20px;
        }
    }
</style>



